import React from 'react';
import closeSvg from './../assets/img/close-icon.svg';


export default ({ portfolioUslug }) => {
  return (
    <>
      <section className="bg-light page-section" id="portfolio">
        <div className="container">
          <div className="row">
            <div className="col-lg-12 text-center">
              <h2 className="section-heading text-uppercase">Usługi</h2>
              <h3 className="section-subheading text-muted">Jaka jest nasza recepta na sukces?</h3>
              <hr class="divider my-5" />
            </div>
          </div>
          <div className="row">
            {
              portfolioUslug && portfolioUslug.map(({ tytul, opis, zdjecie }, index) =>
                <div className="col-md-4 col-sm-6 portfolio-item">
                  <a className="portfolio-link" data-toggle="modal" href={`#portfolioModal${index}`}>
                    <div className="portfolio-hover">
                      <div className="portfolio-hover-content">
                        <i className="fa fa-plus fa-3x"></i>
                      </div>
                    </div>
                    <img className="img-fluid" src={zdjecie} alt="portfolio_img" />
                  </a>
                  <div className="portfolio-caption">
                    <h4>{tytul}</h4>
                    <p className="text-muted">{opis}</p>
                  </div>
                </div>
              )
            }
          </div>
        </div>

      </section>

      {portfolioUslug.map(({ tytul, opis, dluzszyOpis }, index) =>
        <div class="portfolio-modal modal fade" id={`portfolioModal${index}`} tabindex="-1" role="dialog" aria-hidden="true">
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="close-modal" data-dismiss="modal"><img width="52px" src={closeSvg} alt="Zamknij okno" /></div>
              <div class="container">
                <div class="row justify-content-center">
                  <div class="col-lg-8">
                    <div class="modal-body">

                      <h2 class="text-uppercase">{tytul}</h2>
                      <p class="item-intro text-muted">{opis}</p>
                      <img class="img-fluid d-block mx-auto" src="../src/assets/img/portfolio/01-full.jpg" alt="" />
                      <p>{dluzszyOpis}. Lorem ipsum dolor sit amet, consectetur adipisicing elit. Est blanditiis dolorem culpa incidunt minus dignissimos deserunt repellat aperiam quasi sunt officia expedita beatae cupiditate, maiores repudiandae, nostrum, reiciendis facere nemo!</p>
                      {/* <ul class="list-inline">
                        <li>Coś tam: jakiś tekst?</li>
                        <li>Coś tam: jakiś tekst?</li>
                        <li>Coś tam: jakiś tekst?</li>
                      </ul> */}
                      <button class="btn btn-primary" data-dismiss="modal" type="button"><i class="fa fa-times mr-1"></i>Zamknij</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  )
}