import React from 'react';

export default ({ opisZespolu }) => {
    return (
        <>
            <section className="bg-light page-section" id="team">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12 text-center">
                            <h2 className="section-heading text-uppercase">Nasz zespół</h2>
                            <h3 className="section-subheading text-muted">Razem osiągniemy szczyt</h3>
                            <hr class="divider my-5" />
                        </div>
                    </div>
                    <div className="row">
                        {
                            opisZespolu.map(({ imie, opis, zdjecie, doswiadczenie }, index) =>
                                <div className="col-sm-4">
                                    <div className="team-member">
                                        <img className="mx-auto rounded-circle" src={zdjecie} alt="" />
                                        <h4>{imie}</h4>
                                        <p className="text-muted">{opis}</p>
                                        <p className="text-muted"><i>{doswiadczenie}</i></p>
                                    </div>
                                </div>

                            )
                        }
                    </div>
                    <div className="row">
                        <div className="col-lg-8 mx-auto text-center">
                            <p className="large text-muted">Jakaś górnolotna sentencja o zagraniu ekipy itd. Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aut eaque, laboriosam veritatis, quos non quis ad perspiciatis, totam corporis ea, alias ut unde.</p>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}